<template>
  <div class="share">
    <div id="player"></div>
    <div class="user" v-if="isLive" @click.stop="download">
      <img :src="anchor.avatar || $store.state.defaultAvatar" alt="" class="avatar">
      <div class="info">
        <p>{{ anchor.nick_name }}</p>
        <span><img src="./img/hot@2x.png" alt="">{{ live.hot }}</span>
      </div>
      <img class="heart" src="./img/button_guanzhu@2x.png" alt="">
    </div>
    <img v-if="showPlayBtn && isLive" @click="doPlay" src="./img/play@2x.png" alt="" id="play_btn" class="play">
    <div class="live-end" v-if="!isLive">
      <h4>直播即将开始</h4>
      <img :src="anchor.avatar || $store.state.defaultAvatar" alt="">
      <p>{{ anchor.nick_name }}</p>
      <a href="/download">进入钠斯直播</a>
    </div>
    <bottom-download/>
  </div>
</template>

<script>
import BottomDownload from '../../components/bottom-download/bottom-download'
import { post } from '../../common/js/http'

export default {
  name: 'share',
  data() {
    return {
      showPlayBtn: true,
      isLive: false,
      anchor: {},
      live: {}
    }
  },
  components: {
    BottomDownload
  },
  created() {
    this.getDetail()
  },
  mounted() {
    // this.createPlayer()
  },
  beforeDestroy () {
    document.removeEventListener('WeixinJSBridgeReady', this.wxPlay)
    document.body.removeEventListener('touchstart', this.doPlay)
  },
  methods: {
    getDetail() {
      post('/webapi/live/getLivePageData', {
        anchorid: this.$route.params.id
      })
        .then(({ data: { data } }) => {
          this.anchor = data.anchor
          this.live = data.live
          if (data.live) {
            this.isLive = true
            this.$nextTick(() => {
              this.createPlayer()
            })
          }
        })
    },
    createPlayer() {
      let m3u8 = ''

      if (this.live.pull_url.indexOf('flv') >= 0) {
        m3u8 = this.live.pull_url.replace('flv', 'm3u8')
      }

      if (this.live.pull_url.indexOf('m3u8') >= 0) {
        m3u8 = this.live.pull_url
      }

      this.player = new window.TcPlayer('player', {
        width: '100%',
        height: '100%',
        poster: { src: this.live.thumb, style: 'cover' },
        controls: 'none',
        autoplay: false,
        rePlay: true,
        x5_video_position: 'center',
        x5_type: 'h5',
        mp4: this.live.pull_url.indexOf('mp4') >= 0 ? this.live.pull_url : '',
        m3u8
      })

      document.addEventListener('WeixinJSBridgeReady', this.wxPlay)

      document.body.addEventListener('touchstart', this.doPlay)
    },
    doPlay() {
      if (this.player.playing()) return
      this.player.play()
      setTimeout(() => {
        if (this.player.playing()) {
          this.showPlayBtn = false
        }
      }, 200)
    },
    wxPlay() {
      this.player.play()
      setTimeout(() => {
        if (this.player.playing()) {
          this.showPlayBtn = false
        }
      }, 200)
    },
    download() {
      window.location.href = '/download'
    }
  }
}
</script>

